import React, {Component} from 'react';
import ReactAnimationFrame from 'react-animation-frame';
import {DateTime} from 'luxon';

const words = [
    '▸','▸','▸','香','港','人','加','油', '.',
    '▸','▸','▸','香','港','人','加','油', '.',
    '▸','▸','▸','香','港','人','加','油', '.',
    '▸','▸','▸','Free', 'Hong', 'Kong', '.',
    '▸','▸','▸','Free', 'Hong', 'Kong', '.',
    '▸','▸','▸','Free', 'Hong', 'Kong', '.',
    // Comment out for 自由發揮
    // '▸','▸','▸','光','復','香','港','時','代','革','命','.',
    // '▸','▸','▸','光','復','香','港','時','代','革','命','.',
    // '▸','▸','▸','光','復','香','港','時','代','革','命','.'
]

class Timer extends Component {

    prev = 0;

    constructor(props) {
        super(props);
        this.state = {step: 0, diff: null, current: null, demo: false, content: {curr: '', full: '', active: false} };
        this.schedule = DateTime.fromObject({minute: 0, zone: 'Asia/Hong_Kong'}).plus({hours: 1});
        this.target = this.schedule;
        // this.target = DateTime.fromObject({zone: 'Asia/Hong_Kong'})
      }

    onAnimationFrame(time) {
        if (time - this.prev > 200) { 
            this.prev = time;
            let curr = DateTime.fromObject({zone: 'Asia/Hong_Kong'});
            this.setState({
                current: curr,
                diff: this.target.diff( curr )
            }, () => {
                let d = this.state.diff ? this.state.diff : null;
                let dobj = d ? d.toObject() : null;
                this.stepper( -dobj.milliseconds );
            })
        }
    }

    stepper( ms, step=400 ) {
        return this.getWord( Math.floor(ms/step) );
    }

    getWord( index ) {
        if (index >= 0 && index < words.length ) {
            let start = words.lastIndexOf( ".", index ) + 1;
            let end = words.indexOf( ".", index );
            this.setState({content: {
                full: words.slice( start, end ),
                curr: words.slice( start, index+1 ),
                active: true
            }});
        } else {

            if (index >= words.length ) {
                this.schedule = DateTime.fromObject({minute: 0, zone: 'Asia/Hong_Kong'}).plus({hours: 1});
            }

            this.setState( {content: {
                full: `各自努力, ${this.schedule.toFormat("h:mm")}再見`,
                curr: '',
                active: false,
            }});

            if (index > 0 && this.state.demo) {
                this.toggleDemo();
            }
        }
    }

    toggleDemo() {
        this.setState({demo: !this.state.demo}, () => {
            this.target = this.state.demo ? DateTime.fromObject({zone: 'Asia/Hong_Kong'}) : this.schedule;
        });
    }
 
    render() {

        let d = this.state.diff ? this.state.diff : null;
        let dobj = d ? d.toObject() : null;
        
        return (
            <div className="timer">
                <div className="curr-time">
                    <p>{ this.state.current && !this.state.demo ? '香港時間 ' + this.state.current.toLocaleString( DateTime.DATETIME_MED ) : '' }</p>
                    <h2>{ d && !this.state.demo && dobj.milliseconds > 0 ? '倒數 ' + d.toFormat("hh:mm:ss") : '' }</h2>
                </div>  
                <div className="banner">{ d ? this.state.content.full : '' }</div>
                <div className="banner highlight">{ d ? this.state.content.curr : '' }</div>
                { d && dobj.milliseconds > 0 
                    ? <div className="quotation">無論你在何方，每隔一小時，為香港人加油吶喊，讓自由和公義傳遍在香港每個角落！<br /></div>
                    : '' }

                { this.state.content.active && !this.state.demo ? '' :
                    <div className={`demo ${this.state.demo ? 'active' : ''}`} onClick={this.toggleDemo.bind(this)}>
                        { this.state.demo ? <div><span>（試用中）約定你{this.schedule.toFormat("h:mma")}，為香港人加油，吶喊，鳩叫！</span><span className="close">OK</span></div> : '試一試' }
                    </div>
                }
            </div>
        );
    }
}
 
export default ReactAnimationFrame(Timer);